let videoAd = null;
let adVideoUtils = {
  /**
   * @param {String} adUnitId id
   * videoAdInit 
   */
  videoAdInit(adUnitId) {
    if (videoAd) {
      videoAd = null;
    }
    if (uni.createRewardedVideoAd) {
      videoAd = uni.createRewardedVideoAd({
        adUnitId: adUnitId,
      });
      if (videoAd) {
        videoAd.onError((err) => {
          console.log(err);
        });
      }
      // return videoAd;
    }
  },
  /*  true */
  videoAdShow(callback) {
		
    return new Promise((resolve, reject) => {
      adVideoUtils._showAd().then((val) => {
        if (val) {
					callback && callback();
          videoAd.onClose((res) => {
						videoAd.offClose();
            if (res.isEnded) {
              // 
              resolve(true);
            } else {
              resolve(false);
							uni.showToast({
								icon: 'none',
								title: ''
							});
            }
          });
          videoAd.onError((err) => {
            if (err.errCode == "1004") {
              reject("1004");
            } else {
              reject(err);
            }
          });
        } else {
          reject(err);
        }
      });
    });
  },
  _showAd() {
    return new Promise((resolve) => {
      videoAd
        .show()
        .then(() => {
          console.log("");
          resolve(true);
        })
        .catch((err) => {
          console.log("", err);
          // 
          videoAd
            .load()
            .then(() => {
              console.log("");
              resolve(true);
              // 
              return videoAd.show();
            })
            .catch((err) => {
              resolve(false);
              console.log("2", err);
              // this.showUToast("", "error");
            });
        });
    });
  },
};
export default adVideoUtils;